body {
    text-align: center;
    background: linear-gradient(135deg,#b6f09c 20%, #47bbba);
}

input {
    border: none;
    text-align: center;
    font-size: 20pt;

    color: #333;
}

button {
    /* border: none;
    background-color: #b6f09c; */
    
    transition: .2s;
    border: 1px #b6f09c solid;
    background-color: #fff2;
    color: #b6f09c;

    margin-top: 20px;
    padding: 10px 30px;
    border-radius: 10px;
    font-size: 20px;
}

button:hover {
    background-color: #b6f09c;
    color: #fff;
    text-shadow: 1px 1px 4px #0005;
}

button:active {
    background-color: #47bbba;
}


.container {
    margin: 10px;
}

.inputArea {
    padding: 50px;
    border-radius: 20px;
}

.listArea {
    padding: 10px 50px;
    border-radius: 20px;

    background-color: #fffc;
    color: #333;
}


@media only screen and (min-width: 600px) {
    .inputArea, .listArea {
        width: 700px;
    }
}